import React, { Component } from 'react';
import {
  HashRouter,
  Route,
  Link
} from 'react-router-dom';
import {LinkContainer} from 'react-router-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import { Navbar, Nav, NavItem } from 'react-bootstrap';

// import Scripts from './Scripts';
import Blocks from './Blocks';
import EmojiList from  './Emojis';
import AboutPage from './About'
import {CharacterPage} from './Characters';
import './App.css';

class App extends Component {
  render() {
    return(
      <HashRouter>
        <div>
          <Navbar fixedTop>
            <Navbar.Header>
              <LinkContainer to="/">
                <Navbar.Brand>
                  Unicode Explorer
                </Navbar.Brand>
              </LinkContainer>
              <Navbar.Toggle />
            </Navbar.Header>
            <Navbar.Collapse>
              <Nav>
                <LinkContainer to="/blocks">
                  <NavItem>
                    Character Blocks
                  </NavItem>
                </LinkContainer>
                <LinkContainer to="/emoji">
                  <NavItem>
                    Emoji
                  </NavItem>
                </LinkContainer>
              </Nav>
              <Nav pullRight>
                <LinkContainer to="/about">
                  <NavItem>
                    About
                  </NavItem>
                </LinkContainer>
                <NavItem href="https://github.com/weinshel/unicodeexplorer" target="_blank">GitHub</NavItem>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          
          <div className="container containerInner">
            <Route exact path="/" component={Home}/>
            {/* <Route path="/scripts" component={Scripts}/> */}
            <Route path="/blocks" component={Blocks}/>
            <Route path="/emoji" component={EmojiList}/>
            <Route path="/characters/:name" component={CharacterPage}/>
            <Route path="/about" component={AboutPage}/>
          </div>
        </div>
      </HashRouter>
    );
  }
}

const Home = () => (
  <div>
    <h1>Unicode Explorer</h1>
    <div className="homeText">
      <p>Explore the Unicode character set!</p>
      <p>See all the the characters in a specific block, such as <Link to={{pathname: '/blocks/cuneiform'}}>Cuneiform</Link> or <Link to={{pathname: '/blocks/cherokee'}}>Cherokee</Link>. Learn about a specific character such as <Link to={{pathname: '/characters/77952'}}>ìÇÄ</Link>. See <Link to={{pathname: '/emoji'}}>all the emoji</Link>, or view details about <Link to={{pathname: '/emoji/smiling-face-with-horns'}}>üòà</Link>.</p>
    </div>
  </div>
)

export default App;